<template>
	<div class="inputMsg_w">
		<router-view></router-view>
		
		<!-- common part -->
		<div class="common_wrap ps0">
			<div class="page_index">
				<span class="zero">0</span>
				<DigitRoll ref='digitroll' :rollDigits="rollNum" :flipStra="false" ></DigitRoll>
			</div>
			<div class="circle_change ps0">
				<img src="../assets/answer/dot.png" class="dot_bg">
				<img src="../assets/answer/circle.png" class="circle">
			</div>
			<div class="click_btns flex">
				<div class="prev_btn click_btn flex" @click="prevFunc(answer_index)">
					<div class="deco flex"></div>
					<span class="one">上一步 prev</span>
				</div>
				<div class="next_btn click_btn flex" @click="nextFunc(answer_index)">
					<div class="deco flex"></div>
					<span>下一步 next</span>
				</div>
			</div>
		</div>
	</div>
</template>

<style type="text/css" scoped>
	.inputMsg_w{
		width: 100%;
		height: 100%;
		position: relative;
		overflow: hidden;
	}
	.common_wrap{
		height: 100%;
		overflow: hidden;
	}
	.ps0{
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}
	.circle_change img{
		position: absolute;
		top: 0;
	}
	.dot_bg {
		left: 0;
		object-fit: cover;
	}
	.circle{
		width: 16.34rem;
		position: absolute;
		top: 0;
		right: 0;
	}
	.page_index{
		width: .38rem;
		position: absolute;
		font-size: .18rem;
		color: #ff392c;
		display: flex;
		align-items: center;
		justify-content: center;
		top: 0;
		bottom: 0;
		margin: auto;
		left: .2rem;
		z-index: 2;
	}
	.page_index:before, .page_index:after {
		content: '';
		width: 2px;
		height: .55rem;
		display: block;
		background-color: #ff392c;
		position: absolute;
	}
	.page_index:before{
		left: 45%;
		top: 43%;
	}
	.page_index:after{
		right: 49%;
		bottom: 43%;
	}
	.page_index .zero {
		line-height: 1.8
	}
	.d-roll-wrapper{
		width: 30%;
		margin: 0;
	}
	.flex {
		display: flex;
		align-items: center;
	}
	.click_btns{
		width: 100%;
		height: .2rem;
		position: absolute;
		bottom: .35rem;
		justify-content: space-between;
	}
	.click_btns span{
		color: #a3a3a3;
		font-size: .18rem;
	}
	.click_btn {
		justify-content: center;
		cursor: pointer;
	}

	.prev_btn{
		margin-left: .5rem;
	}
	.next_btn{
		margin-right: .5rem;
	}
	.deco{
		width: .31rem;
		height: .08rem;
		background-color: #ff392c;
		border-radius: .12rem;
		position: relative;
		margin-right: .05rem;
	}
	.prev_btn:hover .deco:before {
		transform: translateX(-.1rem);
	}
	.next_btn:hover .deco:before {
		transform: translateX(.1rem);
	}
	.deco:before {
		content: '';
		width: .16rem;
		height: .04rem;
		background-color: #fff;
		display: block;
		border-radius: .1rem;
		transition: transform .2s ease;
	}
	.prev_btn .deco:before{
		margin-right: 8%;
	}
	.prev_btn .deco{
		justify-content: flex-end;
	}
	.next_btn .deco:before{
		margin-left: 8%;
	}



</style>

<script type="text/javascript">
import DigitRoll from '@huoyu/vue-digitroll'
import bus from "@/bus.js"
import {mapState, mapGetters, mapMutations} from 'vuex' //引入state，getters，mutations
import anime from 'animejs/lib/anime.es.js';

export default {
	name: 'inputMsg',
	props: {

	},
	components: { DigitRoll },
	data(){
		return {
			
		}
	},
	watch: {
		answer_index: 'circleAniFunc'
	},
	computed: {
		...mapState(['answer_index']),
		rollNum: function() {
			return this.answer_index
		},
	},
	mounted: function() {
		this.circleAniFunc()
	},
	methods: {
		...mapMutations(['setAnswerIndex']),

		prevFunc: function(index) {
			const me =this;
			// 返回上一页
			me.$router.go(-1)
			me.setAnswerIndex(index-1)
		},
		nextFunc: function(index) {
			const me =this;
			// 下一页
			if(index === 5) {
				// 进入到录入信心页
				// do sth
			}else if(index < 5) {
				me.$router.push({
					path: '/inputMsg/q' + (index+1) 
				})
			}
			me.setAnswerIndex(index+1);
		},
		circleAniFunc: function() {
			const me = this;
			// 动画添加
			anime({
				targets: '.circle',
				easing: 'easeInOutQuad',
				duration: 1500,
				translateX: function() {
					return (me.answer_index-1)*2 +'rem'
				},
				translateY: function() {
					return  -(me.answer_index-1)*2 +'rem'
				},
			});
		}
	}
}

</script>